@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  font-size: 15px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  width: 320px;
  margin: 0 auto;
}
.reset-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  width: 259px;
  height: 52px;
  text-align: center;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
}
.button--transparent {
  color: #444444;
  background-color: transparent;
  border: 2px solid #444444;
}
.button--transparent:hover {
  color: #ffffff;
  background-color: #444444;
  outline: none;
}
.button--transparent:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
  outline: none;
}
.button--tiffani {
  color: #ffffff;
  background-color: #63d1bb;
  border: 2px solid #63d1bb;
}
.modal {
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
}
.page-header {
  position: relative;
  width: 320px;
  text-align: center;
  background-color: #ffffff;
}
.page-header__logo {
  display: block;
  width: 86px;
  margin: 21px auto;
}
.page-header__logo--inner:hover {
  opacity: 0.6;
}
.page-header__logo--inner:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .page-header {
    display: flex;
  }
  .page-header__toggle {
    display: none;
  }
  .page-header__close {
    display: none;
  }
  .page-header__logo {
    width: 112px;
    margin-left: 49px;
    margin-top: 22px;
    margin-bottom: 18px;
    margin-right: 209px;
  }
  .page-header__logo-image {
    width: 112px;
  }
}
@media (min-width: 1150px) {
  .page-header__logo {
    position: absolute;
    z-index: 100;
    width: 148px;
    margin: 0 102px 0 500px;
    padding-top: 9px;
  }
  .page-header__logo-image {
    width: 148px;
  }
}
.main-nav {
  text-align: left;
  background-color: #ffffff;
}
.main-nav__toggle {
  display: block;
  position: absolute;
  top: 7px;
  right: 29px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  outline: none;
}
.main-nav--closed .main-nav__toggle {
  background-image: url("../img/icon-menu-open.svg");
  background-position: center;
}
.main-nav--opened .main-nav__toggle {
  background-image: url("../img/icon-menu-close.svg");
}
.site-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.site-list--additional {
  display: none;
}
.main-nav__delivery {
  display: none;
}
.site-list__item {
  position: relative;
  display: flex;
  padding: 23px 57px 31px 77px;
  border-bottom: 1px solid #ececec;
}
.site-list__item--products {
  border-top: 1px solid #ececec;
}
.site-list__item--search {
  padding: 23px 10px 31px 32px;
}
.site-list__item--cart {
  padding: 23px 10px 31px 29px;
  border: none;
}
.site-list__link {
  position: relative;
  display: flex;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  text-decoration: none;
  color: #000000;
}
.site-list__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.site-list__link:hover svg {
  opacity: 1;
  color: #63d1bb;
}
.site-list__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.site-list__link:active svg {
  opacity: 0.3;
  fill: #63d1bb;
}
.site-list__image {
  display: block;
  margin-top: 4px;
  margin-right: 27px;
  opacity: 0.2;
}
.site-list__image:hover {
  opacity: 1;
  fill: #63d1bb;
}
.site-list__image:active {
  opacity: 0.3;
  fill: #63d1bb;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
  .site-list {
    display: flex;
  }
  .site-list:first-child {
    position: absolute;
    left: 20px;
    bottom: -64px;
    z-index: 100;
  }
  .site-list--additional {
    display: none;
  }
  .main-nav__delivery {
    position: absolute;
    right: 50px;
    bottom: -57px;
    z-index: 100;
    display: block;
    line-height: 24px;
    color: #444444;
  }
  .main-nav__wrapper {
    display: flex;
  }
  .container {
    width: 768px;
  }
  .site-list__item {
    padding: 22px 10px 22px 28px;
    border: none;
  }
  .site-list__item--products a,
  .site-list__item--knitting a {
    font-size: 15px;
  }
  .site-list__item--search {
    padding: 33px 1px 28px 33px;
    border-left: 1px solid #ececec;
  }
  .site-list__item--cart {
    padding: 30px 23px 34px 30px;
    border-left: 1px solid #ececec;
  }
  .site-list__link--search span {
    display: none;
  }
  .site-list__link--search .site-list__image {
    top: 15px;
    left: -38px;
  }
  .site-list__link--add {
    display: inline-block;
    vertical-align: top;
  }
  .site-list__image {
    margin-top: 4px;
    margin-right: 31px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1150px;
  }
  .main-nav__toggle {
    display: none;
  }
  .site-list:first-child {
    position: static;
    margin-right: 304px;
  }
  .site-list--additional {
    position: absolute;
    left: 0;
    bottom: -70px;
    z-index: 100;
    display: flex;
    width: 370px;
  }
  .site-list__item--products a,
  .site-list__item--knitting a {
    font-size: 17px;
  }
  .site-list__item--knitting {
    padding: 30px 22px 22px 34px;
    border-left: 1px solid #ececec;
  }
  .site-list__item--products {
    padding: 31px 36px 22px 49px;
  }
  .site-list__item--new {
    padding: 17px 12px 22px 50px;
  }
  .site-list__item--new a {
    font-size: 15px;
  }
  .site-list__item--sale {
    padding: 16px 4px 28px 50px;
  }
  .site-list__item--sale a {
    font-size: 15px;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
.intro {
  position: relative;
  width: 100%;
  background-color: #f9f9f9;
}
.intro__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.intro__image {
  fill: #149080;
  opacity: 0.5;
}
.intro__header {
  margin-bottom: -70px;
  width: 320px;
  height: 350px;
  padding: 50px 38px;
  text-align: center;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center -21px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .intro__header {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
  }
}
.intro__title {
  font-size: 27px;
  font-weight: bold;
  line-height: 30px;
  color: #010000;
}
.intro__button {
  position: relative;
  display: flex;
  justify-content: space-between;
  min-height: 103px;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  transition: height 1s, transform 1s;
}
.intro__button:hover {
  min-height: 108px;
  transform: translateY(-5px);
}
.intro__button:hover .intro__image {
  opacity: 0.7;
}
.intro__button:active .intro__image {
  opacity: 0.3;
}
.intro__item {
  width: 320px;
}
.intro__item--light a {
  padding: 23px 35px 18px 30px;
  background-color: #63d1bb;
}
.intro__item--light a:hover,
.intro__item--light a:active {
  background-color: #59d5bc;
}
.intro__item--dark a {
  padding: 23px 27px 19px 29px;
  background-color: #56c3b2;
}
.intro__item--dark a:hover,
.intro__item--dark a:active {
  background-color: #46c1ae;
}
@media (min-width: 768px) {
  .intro__title {
    font-size: 43px;
    line-height: 50px;
  }
  .intro__list {
    display: flex;
    padding: 0 50px;
  }
  .intro__item {
    width: 334px;
  }
  .intro__item--light a {
    padding: 27px 35px 40px 37px;
  }
  .intro__item--dark a {
    padding: 28px 33px 30px 36px;
  }
  .intro__header {
    width: 768px;
    height: 746px;
    margin-bottom: -119px;
    padding: 125px 50px;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: center;
  }
  .intro__header::after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 24px;
    background-image: url("../img/bg-zigzag-fill.svg");
    background-size: contain;
  }
  .intro__button {
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .intro__header {
    background-image: url("../img/hero-tablet@2x.jpg");
    background-size: 1150px auto;
  }
}
@media (min-width: 1150px) {
  .intro__title {
    font-size: 53px;
